                {value: 48,name: ''},
                {value: 28, name: ''},
                {value: 9, name: ''}
                ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                normal:{
                  color:function(params) {
                    //
                    var colorList = [
                      '#96C97D', '#FDC65C', '#67E0E3'
                    ];
                    return colorList[params.dataIndex]
                  }
                }
              }
            }
          ]
        })
      }
    },
    watch: {

    },
    components: {
      CountTo
    }

  }
</script>
<style >
  /* WebKitChromeSafari */
  ::-webkit-scrollbar {
    width: 8px; /* 8 */
  }

  ::-webkit-scrollbar-track {
    background-color: #f1f1f1; /*  */
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 4px; /* 4 */
    background-color: #ccc; /*  */
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #999; /*  */
  }
  body{
    background: url("../../assets/bg.png") no-repeat center center;
    background-size: cover;
  }
  .app-container{
    min-height: 800px;
    margin: 0;
    padding: 0;
  }
  .top {
    width: 100%;
    margin: 0 auto;
    background: url("../../assets/title.png") no-repeat center 0;
    background-size: contain;
    height: 80px;
  }
  .title {
    height: 60px;
  }
  .title p{
    text-align: center;
    color:#53E1EB;
    font-size: 22px;
    line-height: 40px;
    vertical-align: middle;
    font-weight: bold;
    margin: 0;
  }
  .main{
    width: 100%;
    height: 700px;
    margin: 0 auto;
  }
  .company-position {
    width: 300px;
    height: 700px;
    background: url("../../assets/company-position-bg.png") no-repeat center center;
    background-size: cover;
    margin-bottom: 40px;
  }
  .position-title {
    width: 100%;
    height: 30px;
    background: url("../../assets/position-title-bg.png") no-repeat center center;
  }
  .position-title p{
    color: #53E1EB;
    font-size: 16px;
    margin: 0;
    margin-top: 5px;
    text-align: center;
    font-weight: bolder;
  }
  .amap-wrapper{
    width: 100%;
    height: 100%;
  }

  .content-window-card p {
    height: 2rem;
  }


  div.info-top {
    position: relative;
    background: none repeat scroll 0 0 #4B6F9E;
    border-bottom: 1px solid #3A557A;
    border-radius: 5px 5px 0 0;
  }

  div.info-top div {
    display: inline-block;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    line-height: 31px;
    padding: 0 10px;
    width: 100%;
    text-align: left;
  }

  div.info-top img {
    position: absolute;
    top: 10px;
    right: 10px;
    transition-duration: 0.25s;
  }

  div.info-top img:hover {
    box-shadow: 0px 0px 5px #fff;
  }

  div.info-middle {
    font-size: 12px;
    padding: 10px 6px;
    line-height: 20px;
    text-align: left;
    border: 1px solid #3A557A;
  }

  div.info-bottom {
    height: 0px;
    width: 100%;
    clear: both;
    text-align: center;
  }

  div.info-bottom img {
    position: relative;
    z-index: 104;
  }

  span {
    margin-left: 5px;
    font-size: 11px;
  }

  .info-middle img {
    float: left;
    margin-right: 6px;
  }

  .map-class{
    background: url('../../assets/map-bg.png') no-repeat center center;
    background-size: cover;
  }

</style>



